@import url(https://fonts.googleapis.com/css2?family=Montserrat);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    font-family: "Montserrat";
}

.MainContainer {
    width:100%;
    display: grid;
    grid-template-columns: 2fr 8fr 2fr;
}

.MainContainer>*:first-child{
    grid-column: 2;
}






.bg1 {
    background-color: #F6F6F6;
}
.About_section{
    min-height:600px;
}

.About_Drones{
    width:100%;
    margin-top:113px;
    display:flex;
    /* gap:81px; */
    flex-wrap: wrap;
    justify-content: space-evenly;
}

.About_DroneCard {
    color:black;
    width:250px;
    display:grid;
    grid-template-rows: 100px 40px 121px;
    text-align: center;
}

.About_DroneCard figure{
    margin:0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.About_DroneCard figure img{
    max-width: 100%;
}

.About_DroneCard h3{
    padding-top:20px;
    margin:0;
    color: #CB0035;
    background: -webkit-linear-gradient(#CB0035, #E06900);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: -webkit-linear-gradient(#CB0035, #E06900);;
}

.About_DroneCard p {
    padding:0px 30px;
    color:#5E6372;
    font-weight: 600;
    font-size: .8em;
}

.About_Button{
    background-color: transparent;
    border-radius: 25px;
    height: 50px;
    min-width:205px;
    max-width:205px;
    box-sizing: border-box;
    border:none;
    font-weight: bold;
    position: relative;
    cursor: pointer;
    background-color: #264699;
    color:white;
}

.About_ButtonBox{
    display:flex;
    justify-content: center;
    align-items: center;
    margin-top:74px;
    margin-bottom:60px;
}


.Drone_section{
    min-height: 600px;
    color:#5E6372;
    padding-top:100px;
}

.Drone_wrapper{
    display: grid;
    grid-template-columns: 5fr 3fr;
    color:#5E6372;
}

.Drone_Title{
    letter-spacing: 0px;
    color: #CA0034;
    font-size: 1.8em;
}
.Drone_Subtitle{
    letter-spacing: 0px;
    color: #5E6372;
    font-weight: 600;
    line-height: 1.8em;
    font-size: .9em;
    margin-bottom: 40px;
}

.Drone_Bulletpoints{
    display:grid;
    grid-template-columns: 3fr 2fr;
    grid-gap:30px;
    gap:30px;
    counter-reset: Bulletpoint;
}
.Drone_Bp{
    font-size: 13px;
    line-height: 1.5rem;
    letter-spacing: 0px;
    color: #A7ACBC;
    display:grid;
    grid-template-columns: 70px 1fr;
    grid-auto-flow: column;
    min-height: 91px;
}
.Drone_Bp:before{
    counter-increment: Bulletpoint; 
    content:counter(Bulletpoint);
    font-weight: bold;
    box-sizing: border-box;
    display:block;
    position: relative;
    color: #3F4E76;
    border: 2px solid #C7CDE0;
    text-align: center;
    border-radius: 25px; 
    font-size: 1.5em;
    width: 50px;
    height: 50px;
    line-height: 50px;
}
.Drone_row{
    display:flex;
    flex-direction: column;
    grid-gap:30px;
    gap:30px;
}


.Drone_wrapper img{
    position: absolute;
    z-index: -1;
    right:0;
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
}

@media (max-width:1300px){
    .Drone_wrapper img{
        display:none;
    }
    .Drone_wrapper{
        grid-template-columns: 1fr;
    }
    .Drone_Bulletpoints{
        grid-template-columns: 1fr 1fr;
    }
    .Drone_Title{
        text-align: center;
    }
    .Drone_Subtitle{
        text-align: center;
    }
}

@media (max-width:800px) {
    .Drone_Bulletpoints{
        grid-template-columns: 1fr;
    }
}
.Footer {
    width:100%;
    height:200px;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-bottom:100px;
}
.Footer p {
    text-align: center;
    font-size: .9em;
    font-weight: bolder;
    color: #A7ACBC;
    margin:5px;
}
.Home_section {
    position: relative;
    min-height:100vh;
    
    z-index: 0;
}

.Home_section:before{
    content: '';
    position:absolute;
    width:100%;
    height:100%;
    background:  url(/DroneZone/static/media/home.f99c5857.png);
    background-position: 0% 160%;
    background-size: 100%;
    background-repeat: no-repeat;
    opacity: .5;
    z-index: -4;
}

.Home_section:after{
    content: '';
    position:absolute;
    width:100%;
    height:100%;
    background: linear-gradient(39deg, #CB0035 0%, #E06900 100%) 0% 0% no-repeat;
    background-position: 0% 160%;
    background-size: 100%;
    background-repeat: no-repeat;
    opacity: 1;
    z-index: -5;
}


.Home_Nav{
    width:auto;
    padding-top:38px;
    height:48px;
    display:flex;
    flex-direction: row;
    box-sizing: border-box;
    position: relative;
}

.Home_Logo{
    font-size:1.6em;
    display:flex;
    align-items: flex-end;
    color:white;
}

.Home_BurgerMenu {
    z-index: 10;
    margin-left: auto;
    font-size: 1.6em;
    cursor: pointer;
    color:white;
}

.Home_Menu{
    display:flex;
    flex-direction: row;
    align-items: flex-end;
    justify-content: flex-end;
    grid-gap: 20px;
    gap: 20px;
}
.Home_MenuMobile{
    position: absolute;
    top: 70px;
    right:0;
    display:flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    grid-gap:15px;
    gap:15px;
}

.Home_Menu a, .Home_MenuMobile a {
    text-decoration:none;
    color:white;
    font-size: .8em;
    opacity: .8;
    transition-duration: .2s;
}
.Home_Menu a:hover{
    opacity: 1;
}

.Home_Slogan{
    color:white;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top:130px;
}

.Home_Slogan h2 {
    text-align: center;
    font-size: 1.9em;
    font-weight: lighter;
}
.Home_Slogan p {
    max-width: 750px;
    text-align: center;
    min-height:92px;
}
.Home_ButtonBox{
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.Home_Button{
    background-color: transparent;
    border-radius: 25px;
    height: 50px;
    min-width:205px;
    box-sizing: border-box;
    border:none;
    font-weight: bold;
    position: relative;
    cursor: pointer;
}

.Home_Button-white{
    color:#D32721;
    background-color: white;;
}
.Home_Button-transparent{
    color:white;
    font-weight: 500;
}

.Home_Button svg{
    position: absolute;
    left:25px;
}

.Home_section>svg {
    position: absolute;
    left:50%;
    bottom: 30px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
}

@media (max-width:1000px) {
    .Home_Nav{
        height: auto;
    }
    .Home_Menu{
        flex-direction: column;
        display:none;
    }
    .Home_section:before{
        background-size: 200%;
        background-position: 50% 100%;
    }
}
@media (min-width: 1000px) {
    .Home_BurgerMenu{
        visibility: hidden;
    }
}
@media (max-width: 640px) {
    .Home_Slogan{
        max-width:100%;
    }
    .Home_ButtonBox{
        flex-direction: column;
    }
    
}
@media(max-height:610px) {
    .Home_section {
        min-height: 700px;
    }
}

.Map_MainContainer {
    width:100%;
    height:482px;
    position: relative;
}
.Map_MainContainer::before{
    content: '';
    position: absolute;
    left:0;
    top:0;
    width:100%;
    height:100%;
    background:  url(/DroneZone/static/media/map.7e1acb91.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 20% 0%;
}

@media (max-width:1200px) {
    .Map_MainContainer::before {
        background-position: 50% 100%;
    }
}

@media (max-width:648px) {
    .Map_MainContainer::before {
        background-position: 55% 100%;
    }
}
.Newsletter_Main{
    height:490px;
    position: relative;
}
.Newsletter_Main:before{
    content:'';
    display:block;
    position: absolute;
    width:100%;
    height:100%;
    background: linear-gradient(45deg,#CB0035,#E06900);
    z-index: -2;
}
.Newsletter_Main:after{
    content:'';
    display:block;
    position: absolute;
    width:100%;
    height:100%;
    background:  url(/DroneZone/static/media/roads.3d2c19fc.png);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 100% 53%;
    opacity: .2;
    z-index: -1;
}

.Newsletter_Wrapper{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.Newsletter_Title{
    color:white;
    font-weight: lighter;
    font-size: 2.5em;
}

.Newsletter_ButtonBox{
    border:1px solid #ffffff32;
    background-color:rgba(255, 255, 255, 0.047);
    border-radius: 25px;
    padding:3px;
    height:50px;
    width:300px;
    box-sizing: border-box;
    position: relative;
}
.Newsletter_ButtonBox input {
    padding-left:13px;
    height:100%;
    width:200px;
    border:none;
    background:none;
    outline: none;
    color:white;
    font-weight: bolder;
    font-size: .95rem;
}
.Newsletter_ButtonBox input::-webkit-input-placeholder {
    color:white;
    font-weight: lighter;
}
.Newsletter_ButtonBox input:-ms-input-placeholder {
    color:white;
    font-weight: lighter;
}
.Newsletter_ButtonBox input::placeholder {
    color:white;
    font-weight: lighter;
}
.Newsletter_ButtonBox button {
    position: absolute;
    top:-1px;
    right:-60px;
    height:50px;
    width: 130px;
    border:1px solid white;
    background-color:white;
    border-radius: 25px;
    color:#D32721;
    font-weight: bold;
    font-size: .95rem;
}

@media (max-width: 648px) {
    .Newsletter_ButtonBox button {
        top:70px;
        left:50%;
        -webkit-transform: translateX(-50%);
                transform: translateX(-50%);
    }
}
@media (max-width: 1000px) {
    .Newsletter_Title{
        text-align: center;
    }
}
.Pics_MainContainer{
    background-color:#F6F6F6;
}
.Pics_Container{
    display:flex;
    flex-direction: column;
}
.Pics_Section{
    display:grid;
    grid-template-columns: 1fr 1fr;
}
.Pics_Section img{
    height:506px;
    position:absolute;
}
.Pics_leftpic{
    width: 50%;
    left:0;
}
.Pics_rightpic{
    width:50%;
    right:0;
}
.Pics_TextBox{
    display:flex;
    flex-direction: column;
    justify-content: center;
    height:506px;
    padding:50px;
    box-sizing: border-box;
    font-size: 14px;
    line-height: 1.6rem;
}
.Pics_TextBox h2{
    color: #CA0034;
    font-size: 1.9em;
}
.Pics_TextBox p{
    color:#5E6372;
    letter-spacing: 0px;
    font-weight: bolder;
    margin-bottom: 55px;
}

@media (max-width:1000px){
    .Pics_Section img{
        width:100%;
        position:absolute;
    }
    .Pics_Section{
        grid-template-columns: 1fr;
    }
    .Pics_TextBox{
        z-index: 5;
    }
    .Pics_TextBox h2{
        text-shadow: 0px 0px 1px black;

    }
    .Pics_TextBox p {
        color:white;
        text-shadow: 0px 0px 3px black;
        font-size: 90%;
    }
}
